.Title{
  background-image: url('../../../images/backGrounds/winter-1.jpg');
  background-size: cover;
  background-position:center; 
  width: 100%;
  height: 100%;
  display: flex;
 flex-direction: column;
justify-content: center; 
align-items: center;

}
.Icon{
  width: 50%; 
  margin: 15px;
}

.IconContainer{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

@media(max-width :499px){
  .Title{
    font-size: 60%
  }
  .Title h1{
    font-size: 35px;
  }
  .Icon{
    width: 5px;
    height: 5px;
    margin: 8px;
  }
}
.DownIcon-1{
  position: absolute;
  width: 30px;
  bottom: 0;
  left:50%;
  margin: 10px;
  }